.App {
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family:Verdana;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 100px;
}

.App-header {
  background-color: #222;
  height: auto;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(720deg); transform: rotate(720deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(720deg); transform: rotate(720deg); }
}

.myButton {
	background-color:#47a3ff;
	border-radius:9px;
	border:1px solid #337bc4;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Verdana;
	font-size:18px;
  padding:10px 20px;
  width: 205px;
	text-decoration:none;
	text-shadow:0px 1px 0px #528ecc;
}

.myButton:hover {
	background-color:#378de5;
}

.myButton:active {
	position:relative;
	top:1px;
}

button:focus {
  outline: 0;
}

button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

h3 {
  font-size: 20px;
}

.summary {
  text-align: left;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  background-color: rgb(250, 250, 250);
  padding: 5px 60px 5px 60px;
	border-radius:9px;
	border:1px solid #dfdfdf;
}

.analysis {
  margin-right: 10%;
  margin-left: 10%;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  margin: 2em;
  padding: 0;
  background-color: gray;
}

.chart-container {
  vertical-align: middle;
  margin: 0 auto;
  max-width: 800px;
}

.chart {
  vertical-align: middle;
  margin: 0 auto;
  margin-bottom: 50px;
  height: 500px;
  background-color: rgb(250, 250, 250);
  padding: 20px;
  /* -moz-border-radius:9px;
	-webkit-border-radius:9px; */
	border-radius:9px;
	border:1px solid #dfdfdf;
}



